import React from 'react'

import { Link } from 'react-router-dom'

import './footer.scss'
import logo from '../../assets/logo.png'

const newMovieLinks = [
    {
        display: 'Phim chiu rp',
        path: '/'
    },
    {
        display: 'Phim l',
        path: '/'
    },
    {
        display: 'Phim b',
        path: '/'
    },
    {
        display: 'Phim hnh ng',
        path: '/'
    },
    {
        display: 'Phim vin tng',
        path: '/'
    },
    {
        display: 'Phim tm l',
        path: '/'
    },
    {
        display: 'Phim hi hc',
        path: '/'
    },
];
const favoriteMovieLinks = [
    {
        display: 'Phim m',
        path: '/'
    },
    {
        display: 'Phim Hn Quc',
        path: '/'
    },
    {
        display: 'Phim Trung Quc',
        path: '/'
    },
    {
        display: 'Phim Thi Lan',
        path: '/'
    },
    {
        display: 'Phim Vit Nam',
        path: '/'
    },
    {
        display: 'Phim ma kinh d',
        path: '/'
    },
    {
        display: 'Phim hot hnh',
        path: '/'
    },
];
const hotMovieLinks = [
    {
        display: 'Phim mi',
        path: '/'
    },
    {
        display: 'Sitemap',
        path: '/'
    },
];
const helperLinks = [
    {
        display: 'Hi p',
        path: '/'
    },
    {
        display: 'Lin h',
        path: '/'
    },
    {
        display: 'Tin tc',
        path: '/'
    },
];
const policyLinks = [
    {
        display: 'iu khon s dng',
        path: '/'
    },
    {
        display: 'Chnh sch ring t',
        path: '/'
    },
    {
        display: 'Khiu ni bn quyn',
        path: '/'
    },
];

const Footer = () => {
    return (
        <div className="footer">
            <div className="container">
                <div className="grid-footer">
                    <div className="grid-footer__col">
                        <img src={logo} alt="" />
                    </div>
                    <div className="grid-footer__col">
                        <p className="grid-footer__col__title">
                            Phim Mi
                        </p>
                        <div className="grid-footer__col__links">
                            {
                                newMovieLinks.map((e, i) => (
                                    <Link to={e.path} key={i} className="grid-footer__col__links__item">{e.display}</Link>
                                ))
                            }
                        </div>
                    </div>
                    <div className="grid-footer__col">
                        <p className="grid-footer__col__title">
                            Phim Hay
                        </p>
                        <div className="grid-footer__col__links">
                            {
                                favoriteMovieLinks.map((e, i) => (
                                    <Link to={e.path} key={i} className="grid-footer__col__links__item">{e.display}</Link>
                                ))
                            }
                        </div>
                    </div>
                    <div className="grid-footer__col">
                        <p className="grid-footer__col__title">
                            Phim Hot
                        </p>
                        <div className="grid-footer__col__links">
                            {
                                hotMovieLinks.map((e, i) => (
                                    <Link to={e.path} key={i} className="grid-footer__col__links__item">{e.display}</Link>
                                ))
                            }
                        </div>
                    </div>
                    <div className="grid-footer__col">
                        <p className="grid-footer__col__title">
                            Tr gip
                        </p>
                        <div className="grid-footer__col__links">
                            {
                                helperLinks.map((e, i) => (
                                    <Link to={e.path} key={i} className="grid-footer__col__links__item">{e.display}</Link>
                                ))
                            }
                        </div>
                    </div>
                    <div className="grid-footer__col">
                        <p className="grid-footer__col__title">
                            Thng tin
                        </p>
                        <div className="grid-footer__col__links">
                            {
                                policyLinks.map((e, i) => (
                                    <Link to={e.path} key={i}className="grid-footer__col__links__item">{e.display}</Link>
                                ))
                            }
                            <p> 2021 PhimChill.Net</p>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Footer
